import container from '../../../components/LayOut/LayOut.module.css'
import style from './qrCode.module.css'
import QRcode from '../../../images/qr-code.png'


const QRCode = () => {
    //   QR-code  Api
    return (
        <div className={container.container}>
            <section className={style.wrapper}>
                <div>,<br/>Caffesta Support!</div>
                <div className={style.titleQr}>  QR-</div>
                <div ><img className={style.qrWrapper} src={QRcode} alt={'QRcode'}/></div>
                <div className={style.descrBottom}>       </div>
            </section>
        </div>
    )

}

export default QRCode
